@use "../../components/partials/partial";

.dash {
  .top,
  .sf {
    @include partial.flex;
    .date {
      background-color: var(--white);
      padding: 1rem;
    }
  }
  .boxes {
    @include partial.flex($gap: 0.5rem, $justify: left);
    margin-top: 1.5rem;
  }
  .add-item {
    @include partial.flex($gap: 0.5rem, $justify: center);
    margin-top: 3rem;
  }
  input,
  select {
    @include partial.input($display: inline, $width: auto, $mTop: 0rem);
    font-family: "Inter", sans-serif;
  }
  input:focus,
  select:focus,
  .search:focus {
    border-bottom: 2px solid var(--blue);
  }
  .sf {
    margin-top: 2rem;
    .search {
      @include partial.flex($gap: 0.2rem, $justify: left);
      background-color: var(--white);
      width: 35rem;
      padding: 0.5rem 1rem;
      border-radius: 0.5rem;
      input {
        @include partial.input(
          $display: inline,
          $mTop: 0rem,
          $border: none,
          $bRadius: 0
        );
      }
    }

    select {
      height: 5rem;
    }
  }
  #name {
    text-transform: capitalize;
  }
  #warning {
    @include partial.pText($width: 100%);
    color: var(--red);
    margin-top: 1.5rem;
    text-align: center;
  }
  select {
    color: var(--white);
    background-color: var(--blue);
  }
}

@media screen and (max-width: 735px) {
  .dash {
    .top {
      font-size: 1.8rem;
      @include partial.flex($dir: column);
    }
    input,
    select {
      @include partial.input($display: inline, $width: 100%, $mTop: 0rem);
    }
    .sf {
      div {
        width: 50%;
      }
      input,
      select {
        font-size: 1.8rem;
      }
    }
    .boxes {
      @include partial.flex($dir: column, $gap: 1rem, $align: flex-start);
    }
    .add-item {
      select,
      input,
      button {
        font-size: 1.8rem;
      }
      @include partial.flex(
        $gap: 0.5rem,
        $justify: left,
        $align: flex-start,
        $dir: column
      );
    }
  }
}

@media screen and (max-width: 500px) {
  .dash {
    .sf {
      div:nth-child(1) {
        width: 40%;
      }
      div:nth-child(2) {
        width: 60%;
      }
    }
  }
}
@media screen and (max-width: 400px) {
  .dash {
    .sf {
      @include partial.flex($dir: column);
      div {
        width: 100% !important;
      }
    }
  }
}
